import androidx.compose.runtime.CompositionLocalProvider
import com.varabyte.kobweb.core.AppGlobalsLocal
import com.varabyte.kobweb.navigation.RoutePrefix
import com.varabyte.kobweb.navigation.Router
import com.varabyte.kobweb.navigation.UpdateHistoryMode
import com.varabyte.kobweb.silk.components.animation.registerKeyframes
import com.varabyte.kobweb.silk.defer.renderWithDeferred
import kotlin.Unit
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.compose.web.renderComposable

public fun main(): Unit {
    RoutePrefix.set("/kobweb-ghp-demo/")
    val router = Router()
    com.varabyte.kobweb.core.init.initKobweb(router) { ctx ->
        ctx.router.register("/") { org.example.pages.HomePage() }
        ctx.router.register("/about") { org.example.pages.AboutPage() }

    }
    router.addRouteInterceptor {
        path = path.removeSuffix(".html").removeSuffix(".htm")
    }

    com.varabyte.kobweb.silk.init.additionalSilkInitialization = { ctx ->
        com.varabyte.kobweb.silk.init.initSilkWidgets(ctx)
        com.varabyte.kobweb.silk.init.initSilkWidgetsKobweb(ctx)
        ctx.theme.registerComponentStyle(org.example.HeadlineTextStyle)
        ctx.theme.registerComponentStyle(org.example.SubheadlineTextStyle)
        ctx.theme.registerComponentStyle(org.example.components.layouts.MarkdownStyle)
        ctx.theme.registerComponentStyle(org.example.components.layouts.PageContentStyle)
        ctx.theme.registerComponentStyle(org.example.components.sections.FooterStyle)
        ctx.theme.registerComponentStyle(org.example.components.sections.NavHeaderStyle)
        ctx.theme.registerComponentStyle(org.example.pages.HeroContainerStyle)
        ctx.theme.registerComponentStyle(org.example.pages.HomeGridStyle)
        ctx.theme.registerComponentStyle(org.example.pages.HomeGridCellStyle)
        ctx.theme.registerComponentVariants(org.example.CircleButtonVariant)
        ctx.theme.registerComponentVariants(org.example.UncoloredButtonVariant)
        ctx.stylesheet.registerKeyframes(org.example.components.sections.SideMenuSlideInAnim)
        org.example.initColorMode(ctx)
        org.example.initSiteStyles(ctx)
        org.example.initTheme(ctx)
    }

    // For SEO, we may bake the contents of a page in at build time. However, we will
    // overwrite them the first time we render this page with their composable, dynamic
    // versions. Think of this as poor man's hydration :)
    // See also: https://en.wikipedia.org/wiki/Hydration_(web_development)
    router.navigateTo(window.location.href.removePrefix(window.location.origin),
            UpdateHistoryMode.REPLACE)

    val root = document.getElementById("root")!!
    while (root.firstChild != null) { root.removeChild(root.firstChild!!) }

    renderComposable(rootElementId = "root") {
        CompositionLocalProvider(AppGlobalsLocal provides mapOf("title" to "Kobweb GHP Demo")) {
            org.example.AppEntry {
                router.renderActivePage { renderWithDeferred { it() } }
            }
        }
    }
}
